import React, { useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import { Helmet } from "react-helmet";
import AOS from "aos";
import "aos/dist/aos.css";
const About = () => {
  useEffect(() => {
    AOS.init();
  }, []);
  return (
    <div>
      <Container id="section2">
        <Row>
          <Col>
            <h1 style={{ textAlign: "center", paddingTop: "30px" }}>About</h1>
          </Col>
        </Row>
        <Row
          style={{
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
          }}
        >
          <Col
            lg={6}
            md={6}
            sm={12}
            style={{ paddingBottom: "30px", textAlign: "center" }}
          >
            <img
              alt="about"
              className="borderPicture"
              style={{
                borderRadius: "10px",
                width: "50%",
                border: "5px solid #fff",
              }}
              src="ema.jpeg"
            />
          </Col>
          <Col
            lg={6}
            md={6}
            sm={12}
            data-aos="fade-in"
            data-aos-duration="1000"
            style={{
              position: "relative",
              textAlign: "left",
              padding: "25px",
              background: "#131F55",
              color: "#fff",
              borderRadius: "10px",
            }}
          >
            <h4>
              Hi. I'm Emmanuel Adedeji, Test Analyst and Web Developer
              professional. Nice to meet you.
            </h4>
            <h6>
              Never stop learning" is my mantra and I've proved it even to
              myself taking every opportunity that can lead me to learn
              something more. My excellent understanding of Software Development
              Lifecycle and the role of QA, will boost your projects, and thanks
              to my organizational and intercommunication skills I will
              definetly be a valuable team player.
            </h6>
            <a href="https://drive.google.com/file/d/1cL__2H9-1ByKHT1HJngL-sXFX6CMevH1/view?usp=sharing">
              <img
                alt="about"
                className="cv"
                style={{
                  position: "absolute",
                  width: "50px",
                  right: "-10px",
                  bottom: "-10px",
                }}
                src="data:image/svg+xml;base64,"
              />
            </a>
          </Col>
        </Row>
        <Helmet>
          <meta charSet="utf-8" name="description" content="Hi. I'm Emmanuel Adedeji, Test Analyst and Web Developer
              professional. Nice to meet you."/>
          <title>I'm Emmanuel Adedeji</title>
          <link rel="canonical" href="https://fawtech.org/" />
        </Helmet>
      </Container>
    </div>
  );
};
export default About;
